.icons {
   top: 20px;
   right: 32px;
   height: 32px;
   background-color: transparent;
   border: none;
   cursor: pointer;
   border-radius: 50%;
   background-color: #3E4356;
   box-shadow: 0 0px 15px 2px rgba(180, 180, 180, 0.25);

   .iconSun {
      display: block;
      font-size: 32px;
   }

   .iconMoon {
      display: block;
      font-size: 32px;
      color: #fff;
   }

   .click__icon {
      display: none;
   }
}
.light {
   background-color: #fff;
}

.dark {
   background-color: #17171E;
   color: #fff;
}
.section{
   .video__left{
      height: 95vh;
      overflow: auto;
      width: 350px;
      position: fixed;
      top: 100px;
      right: 0;

      .section__list {
         font-size: 16px;
         .video__item{
            margin-bottom: 50px;
         }

         .videos__title{
            width: 300px;
            color: #19202C;
            margin-bottom: 10px;

            body.dark &{
               color: #fff !important;
            }
         }
      }
   }
}
.ifreme{
   width: 700px;
   padding-bottom: 50px;

   .ifreme__title {
      color: #192646;
      font-size: 36px;
      line-height: 45px;
      margin-top: 20px;
      margin-bottom: 15px;
      body.dark &{
         color: #fff;
      }
   }
   .ifreme__btns{
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-bottom: 1px solid rgba(216, 216, 216, 0.5);
      padding-bottom: 25px;
      .count__views{
         font-size: 14px;
         line-height: 25px;
         color: #C2C2C2;
      }

      .btn{
         .like, .unlike, .shere{
            cursor: pointer;
            background-color: transparent;
            border: none;
            color: #C2C2C2;
            margin-left: 30px;
         }
      }
   }

   .ifreme__follow{
      margin-top: 20px;
      display: flex;
      justify-content: space-between;
      align-items: flex-start;

      .ifreme__user{
         display: flex;
         align-items: flex-start;

         .ifreme__subtitle{
            font-size: 26px;
            line-height: 33px;
            color: #19202C;
            margin-left: 12px;

            body.dark &{
               color: #fff !important;
            }
         }
         .ifreme__subtext{
            font-size: 16px;
            line-height: 22px;
            opacity: 0.6;
            margin-left: 12px;
            margin-top: 34px;
            width: 400px;
         }
      }
      .ifreme__follow--btn{
         width: 154px;
         padding: 10px 20px;
         border-radius: 20px;
         border: none;
         background-color: red;
         color: #fff;
         cursor: pointer;  
      }
   }
}