*, *::after, *::before{
   padding: 0;
   margin: 0;
   box-sizing: border-box;
}

body{
   font-family: 'Mulish', sans-serif;
   font-weight: 400;
   font-size: 16px;
   width: 100%;
   height: 100vh;
   overflow: auto;
}
.fill{
   color: #fff !important;
}

a{
   text-decoration: none;
   display: inline-block;
}
ul{
   list-style-type: none;
}

.section{
   margin-left: 260px;
   margin-top: 100px;
}

/* width */
::-webkit-scrollbar {
   width:2px;
   height: 0px;
}

/* Track */
::-webkit-scrollbar-track {
   border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
   background: #eee;
   border-radius: 10px;
}
iframe {
   width: 250px ;
   height: 150px ;
}

.favourite{
   position: absolute;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
   display: flex;
   flex-direction: column;
   align-items: center;
}

.boxIcon {
   color: #a8a8a891 !important;
   font-size: 100px;
}
.favourite__title{
   font-size: 50px;
   color: #a8a8a891;
   text-align: center;
}
.ifreme iframe{
   width: 700px;
   height: 400px;
}
