.menu{
   position: fixed;
   top: 0px;
   left: 0;
   padding: 30px 10px 0 50px;
   height: 100vh;
   overflow-y: auto;
   padding-bottom: 100px;
   background-color: #fff;
   z-index: 200;

   body.dark &{
      background-color: #17171E !important;
   }

   .header__logo--img{
      margin-bottom: 60px;

      body.dark & {
         display: none;
      }
   }
   .header__logo--imgdark{
      margin-bottom: 60px;
      display: none;

      body.dark &{
         display: block;
      }
   }
   .menu__item{
      margin-bottom: 28px;

      .menu__link{
         display: flex;
         align-items: center;
         color: #898989;
         font-size: 16px;
         line-height: 20px;
         fill: #898989;
         transition: color ease-in .3s,
                     fill ease-in .3s;
         &:hover{
            color: #FF0000;
            fill: #FF0000;
         }
      }
      .active{
         color: #FF0000;
         fill: #FF0000;
      }
   }
   .mb{
      margin-bottom: 60px;
   }

   .menu__subs{

      .menu__subs--title{
         font-weight: bold;
         font-size: 18px;
         line-height: 23px;
         margin-bottom: 33px;
      }
      
      .users{
         margin-bottom: 105px;

         .users__item {
            margin-bottom: 23px;

            .users__link {
               display: flex;
               align-items: center;

               &:hover {
                  .users__title {
                     color: #FF0000;
                  }
               }

               .users__img {
                  width: 26px;
                  height: 26px;
                  border-radius: 50%;
                  margin-right: 10px;
                  object-fit: cover;
               }

               .users__title {
                  transition: color ease-in .3s;
                  font-size: 16px;
                  line-height: 20px;
                  color: #898989;
                  font-weight: 400;
               }
            }
         }
      }
   }
}



.icon{
   width: 20px;
   height: 20px;
   margin-right: 15px;
}