.video__list{
   display: flex;
   width: 80vw;
   overflow: auto;
   margin-top: 100px;
   margin-left: 260px;

   .video__item{
      margin-right: 40px;
      position: relative;
      iframe {
         width: 250px !important;
         height: 150px !important;
         margin-bottom: 10px;
      }

      .videos__title {
         width: 250px;
         color: #19202C;

         body.dark &{
            color: #fff !important;
         }
      }

      .videos__view{
         position: absolute;
         bottom: 20px;
         font-size: 13px;
         line-height: 16px;
         color: #C2C2C2;
      }
   }
}