
.header{
   padding: 30px 50px 20px;
   display: flex;
   justify-content: space-between;
   align-items: center;
   background-color: #fff;
   position: fixed;
   top: 0;
   right: 0;
   left: 0;
   width: 100%;
   z-index: 100;
   body.dark &{
      background-color: #17171E !important;
   }
   
   .header__left{
      position: relative;
      display: flex;
      align-items: center;
      
      .header__input{
         font-family: "Mulish";
         width: 700px;
         margin-left: 210px;
         font-size: 16px;
         line-height: 20px;
         padding: 12px 23px;
         border-radius: 22px;
         background: rgba(235, 235, 235, 0.4);
         border: none;
         outline: none;
         
         body.dark &{
            color: #FFFFFF !important;
         }
      }
      &::after {
         content: '';
         display: block;
         position: absolute;
         background: url("../../img/search.svg");
         background-repeat: no-repeat;
         background-size: contain;
         width: 18px;
         height: 18px;
         right: 23px;
         top: 50%;
         transform: translateY(-50%);
         body.dark &{
            background: url("../../img/search-dark.svg") !important;
         }
      }
      body.dark ::placeholder {
         color: #fff !important;
      }
   }
   .header__right{
      display: flex;
      align-items: center;
      
      .header__right--img{
         display: inline-block;
         margin-left: 50px;
      }
   }
}